<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-table :data="PerList" border style="width: 100%">
        <el-table-column type="index" label="" width="50">
        </el-table-column>
        <el-table-column prop="authName" label=""> </el-table-column>
        <el-table-column prop="" label="">order</el-table-column>
        <el-table-column prop="level" label="">
          <template slot-scope="scope">
            <div>
              <el-tag type="" v-if="scope.row.level == 0"></el-tag>
              <el-tag type="success" v-else-if="scope.row.level == 1"
                ></el-tag
              >
              <el-tag type="warning" v-else-if="scope.row.level == 2"
                ></el-tag
              >
            </div>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "Permission",
  data() {
    return {
      path: "list",
      PerList: [],
    };
  },
  mounted() {
    this.GetPerList(this.path);
  },
  methods: {
    async GetPerList(type) {
      const { data: res } = await this.$http.get("/api/rights/" + type);
      console.log(res);
      if (res.msg.status !== 200) return this.$message.error("");
      this.PerList = res.data;
    },
  },
};
</script>

<style lang="less" scoped>
/deep/ .el-breadcrumb__inner {
  color: #fff;
}
/deep/.el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: #fff;
}
.el-table {
  margin: 15px 0 0 0;
}
</style>
