<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="4"
          ><el-button type="primary" @click="Sent"></el-button></el-col
        >
      </el-row>

      <el-table :data="UseList" border style="width: 100%">
        <el-table-column type="index" label="" width="50">
        </el-table-column>
        <el-table-column prop="name" label=""> </el-table-column>
        <el-table-column prop="description" label=""></el-table-column>
        <el-table-column prop="" label="">
          <template slot-scope="scope">
            <div>
              <!--  -->
              <!--  -->
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
              </el-tooltip>

              <!--  -->
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button type="warning" icon="el-icon-setting" size="mini"
                  ></el-button
                >
              </el-tooltip>

              <!--  -->
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="open(scope.row.id)"
                ></el-button>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
          <el-form-item label="" prop="name">
            <el-input autocomplete="off" v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              autocomplete="off"
              v-model="ruleForm.description"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="add"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      UseList: [
        {id:1, name: "", description: "" },
        {id:2, name: "", description: "" },
        {id:3, name: "", description: "" },
        {id:4, name: "", description: "" },
        {id:5, name: "", description: "" },
      ],
      dialogFormVisible: false,
      ruleForm: {
        name: "",
        description: "",
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    open(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then( () => {
        this.UseList=this.UseList.filter(item=>item.id!==id)
        
        this.$message({
          type: "success",
          message: "!",
        });
      });
    },
    Sent() {
      this.dialogFormVisible = true;
    },
    add() {
      // console.log(this.ruleForm.name.length);
      if(this.ruleForm.name.length <=5&&this.ruleForm.name.length >=3){
         const newArr = {
        name: this.ruleForm.name,
        description: this.ruleForm.description,
      };
      this.dialogFormVisible = false;
      this.$message.success("");
      this.ruleForm.name=''
      this.ruleForm.description=''
      this.UseList.push(newArr);
      }else {
        this.$notify({
          title: '',
          message: '',
          type: 'warning'
        });
      }
     
    },
  },
};
</script>

<style lang="less" scoped>
/deep/ .el-breadcrumb__inner {
  color: #fff;
}
/deep/.el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: #fff;
}
.el-table {
  margin: 15px 0 0 0;
}
</style>
