<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- card -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input
            placeholder=""
            :clearable="true"
            @clear="Clear"
            v-model="searchVal"
            @keyup.enter.native ="search"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4"
          ><el-button type="primary" @click="addUser"
            ></el-button
          ></el-col
        >
      </el-row>

      <!--  -->
      <el-table :data="pList" v-if="zt" border style="width: 100%">
        <el-table-column type="index" label="" width="50">
        </el-table-column>
        <el-table-column prop="title" label="" width="350">
        </el-table-column>
        <el-table-column prop="price" label="()" width="180">
        </el-table-column>
        <el-table-column prop="weight" label="(kg)" width="180">
        </el-table-column>
        <el-table-column prop="startTime" label="" width="180">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="open(scope.row.id)"
                ></el-button>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <el-table :data="searchArr" v-if="serzt" border style="width: 100%">
        <el-table-column type="index" label="" width="50">
        </el-table-column>
        <el-table-column prop="title" label="" width="350">
        </el-table-column>
        <el-table-column prop="price" label="()" width="180">
        </el-table-column>
        <el-table-column prop="weight" label="(kg)" width="180">
        </el-table-column>
        <el-table-column prop="startTime" label="" width="180">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="remove(scope.row.id)"
                ></el-button>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
          <el-form-item label="" prop="title">
            <el-input autocomplete="off" v-model="ruleForm.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input autocomplete="off" v-model="ruleForm.price"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input autocomplete="off" v-model="ruleForm.weight"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="OKs"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pList: [],
      id: 9,
      zt: true,
      serzt: false,
      searchArr: [],
      dialogFormVisible: false,
      ruleForm: {
        title: "",
        price: "",
        weight: "",
        startTime: "",
      },
      searchVal: "",
      rules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 20,
            message: " 3  20 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  mounted() {
    this.getpList();
  },
  methods: {
    getpList() {
      this.pList = [
        {
          id: 1,
          title: "",
          price: "999",
          weight: "11",
          startTime: "2022-02-28 10:33:19",
        },
        {
          id: 2,
          title: "10KG",
          price: "2999",
          weight: "10",
          startTime: "2022-02-28 10:33:19",
        },
        {
          id: 3,
          title: "AOTIN/ZKM4-A!",
          price: "11599",
          weight: "105",
          startTime: "2022-02-28 10:33:19",
        },
        {
          id: 4,
          title: "",
          price: "1009",
          weight: "1",
          startTime: "2022-02-28 10:33:19",
        },
        {
          id: 5,
          title: "BCD-219W",
          price: "2299",
          weight: "105",
          startTime: "2022-02-28 10:33:19",
        },
        {
          id: 6,
          title: "",
          price: "1259",
          weight: "3.5",
          startTime: "2022-02-28 10:33:19",
        },
        {
          id: 7,
          title: "651L10kg",
          price: "7399",
          weight: "10",
          startTime: "2022-02-28 10:33:19",
        },
        {
          id: 8,
          title: "108KG",
          price: "589",
          weight: "8",
          startTime: "2022-02-28 10:33:19",
        },
      ];
    },
    addUser() {
      this.dialogFormVisible = true;
    },
    OKs() {
      // 
      if (this.ruleForm.title.length >= 3 && this.ruleForm.title.length <= 20) {
        // 
        const time = new Date();
        // 
        const newTime = time
          .toLocaleString("zh-cn")
          .replaceAll("/", "-")
          .replaceAll(",", "");
        // 
        const newArr = {
          id: this.id++,
          title: this.ruleForm.title,
          price: this.ruleForm.price,
          weight: this.ruleForm.weight,
          startTime: newTime,
        };
        // 
        this.pList.push(newArr);
        // 
        this.dialogFormVisible = false;
        // 
        this.ruleForm.title = "";
        this.ruleForm.price = "";
        this.ruleForm.weight = "";
      } else {
        // 
        this.$notify({
          title: "",
          message: "",
          type: "warning",
        });
      }
    },
    search() {
      this.getpList();
      // 
      this.searchArr = this.pList.filter((item) =>
        item.title.includes(this.searchVal)
      );

      this.zt = false;
      this.serzt = true;
    },
    Clear() {
      this.getpList();
      this.zt = true;
      this.serzt = false;
    },
    open(id) {
      // console.log(id);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then( () => {
        
        console.log(id);
        // this.pList.splice(id-1,1)
        this.pList= this.pList.filter(item=>item.id !==id)
        // console.log(this.pList);
        //  this.pList.filter((item) => item.id !== id);
        this.$message({
          type: "success",
          message: "!",
        });
      });
    },
    remove(id){
        this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then( () => {
        this.searchArr.pop(item=>item.id !==id)
        this.$message({
          type: "success",
          message: "!",
        });
      });
    }
  },
};
</script>

<style lang="less" scoped>
/deep/ .el-breadcrumb__inner {
  color: #fff;
}
/deep/.el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: #fff;
}
.el-table {
  margin: 15px 0 0 0;
}
</style>