<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-button type="primary" @click="Sent"></el-button>

      <el-table
        :data="tableData"
        style="width: 100%; margin-bottom: 20px"
        row-key="id"
        border
        default-expand-all
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
        <!-- <el-table-column prop="id" label="" width="180"> </el-table-column> -->
        <el-table-column prop="name" label="" width="180">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div>
              <el-result
                :icon="scope.row.state"
                style="width: 10px; height: 10px"
              >
              </el-result>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="" label="">
          <template slot-scope="scope">
            <div>
              <el-tag type="" v-if="scope.row.level == 0"></el-tag>
              <el-tag type="success" v-else-if="scope.row.level == 1"
                ></el-tag
              >
              <el-tag type="warning" v-else-if="scope.row.level == 2"
                ></el-tag
              >
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="remove(scope.row.id)"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top"
              >
                <el-button
                  type="warning"
                  icon="el-icon-plus"
                  size="mini"
                ></el-button>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
          <el-form-item label="" prop="name">
            <el-input autocomplete="off" v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="">
            <el-select v-model="ruleForm.state" placeholder="">
              <el-option label="" value="success"></el-option>
              <el-option label="" value="error"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="level">
            <el-select v-model="ruleForm.level" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="add"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
      },
      id: 5,
      ruleForm: {
        id: "",
        name: "",
        state: "",
        level: "",
      },
    };
  },
  created() {
    this.gettableData();
  },
  methods: {
    gettableData() {
      this.tableData = [
        {
          id: 1,
          state: "success",
          name: "",
          level: 0,
        },
        {
          id: 2,
          state: "error",
          name: "",
          level: 1,
        },
        {
          id: 3,
          name: "",
          state: "success",
          level: 2,
          children: [
            {
              id: 31,
              name: "",
              state: "error",
              level: 1,
            },
            {
              id: 32,
              name: "",
              state: "success",
              level: 0,
            },
          ],
        },
        {
          id: 4,
          name: "",
          state: "error",
          level: 2,
        },
      ];
    },
    Sent() {
      this.dialogFormVisible = true;
    },
    add() {
      if (this.ruleForm.name.length >= 3 && this.ruleForm.name.length <= 5) {
        const newArr = {
          id: this.id++,
          name: this.ruleForm.name,
          state: this.ruleForm.state,
          level: this.ruleForm.level,
        };
        this.tableData.push(newArr);
        this.dialogFormVisible = false;
      } else {
        // 
        this.$notify({
          title: "",
          message: "",
          type: "warning",
        });
      }
    },
    remove(id) {
      // console.log(i);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        this.tableData = this.tableData.filter((item) => item.id !== id);
        this.$message({
          type: "success",
          message: "!",
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
/deep/ .el-breadcrumb__inner {
  color: #fff;
}
/deep/.el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: #fff;
}
.el-table {
  margin: 15px 0 0 0;
}
/deep/.el-result__icon svg {
  width: 30px;
  height: 30px;
}
</style>