<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- from -->
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-select
            v-model="form.region"
            placeholder=""
            @change="Digital()"
          >
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <!--  -->
      <el-tabs>
        <el-tab-pane label="" name="first">
          <el-button type="primary" :disabled="state" @click="add"
            ></el-button
          >
          <el-table :data="ParList" border style="width: 100%">
            <el-table-column type="index" label="" width="50">
            </el-table-column>
            <el-table-column prop="title" label="" width="500">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <div>
                  <el-tooltip
                    class="item"
                    effect="dark"
                    content=""
                    placement="top"
                  >
                    <el-button
                      type="primary"
                      icon="el-icon-edit"
                      size="mini"
                    ></el-button>
                  </el-tooltip>
                  <el-tooltip
                    class="item"
                    effect="dark"
                    content=""
                    placement="top"
                  >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      @click="open(scope.row.id)"
                    ></el-button>
                  </el-tooltip>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="second"></el-tab-pane>
      </el-tabs>
      <!--  -->
      <el-dialog title="" width="50%" :visible.sync="dialogVisible">
        <el-form label-width="70px">
          <el-form-item label="">
            <el-input autocomplete="off" v-model="iptVal"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="yes"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      state: true,
      dialogVisible: false,
      form: { region: "" },
      ParList: [
        { title: "" },
        { title: "" },
        { title: "" },
        { title: "" },
      ],
      iptVal: "",
    };
  },
  methods: {
    // value,
    Digital() {
      if (this.form.region == 2) {
        this.state = false;
      }else{
        this.state = true;
      }
    },
    // 
    add() {
      this.dialogVisible = true;
    },
    // ParList
    yes() {
      const newArr = {
        title: this.iptVal,
      };
      this.dialogVisible = false;
      this.ParList.push(newArr);
      this.iptVal = "";
    },
  },
};
</script>

<style lang="less" scoped>
/deep/ .el-breadcrumb__inner {
  color: #fff;
}
/deep/.el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: #fff;
}
.el-table {
  margin: 15px 0 0 0;
}
</style>